import { createStore } from 'vuex'

// Create a new store instance.
const store = createStore({
  state: {
    filterID: '0',
    feedsData: [],
    resultData: {},
    setSearchKey: '',
    pageTheme: false,
    visitsCount: 0,
  },
  mutations: {
    setFilterId(state, data) {
      state.filterID = data
    },
    setFeedsData(state, data) {
      state.feedsData = data
    },
    setResultData(state, data) {
      state.resultData = data
    },
    setSearchKey(state, data) {
      state.setSearchKey = data
    },
    setPageTheme(state, data) {
      state.pageTheme = data
    },
    setVisitsCount(state, data) {
      state.visitsCount = data
    },
  },
})

export default store
