<template>
  <top-bar msg="Welcome to Your Vue.js + TypeScript App" />
  <loading-view v-if="isLoading" />
  <router-view v-else />
  <page-footer />
</template>

<script setup lang="ts">
import { onMounted, ref, toRaw, watch } from 'vue'
import LoadingView from './views/LoadingView.vue'
import TopBar from '@/components/TopBar.vue'
import PageFooter from '@/components/PageFooter.vue'
import { db, writeVisitsCount } from '@/utils/firebase-config'
import { ref as firebaseRef, onValue } from 'firebase/database'
import store from '@/store'

const isLoading = ref(false)
const result = ref(null)
const visitsCount = ref(0)

onMounted(() => {
  isLoading.value = true
  const element = document.getElementById('loader')
  if (element) {
    element.style.display = 'none'
  }
  fetchUserData()
  fetchVisitsCount()
})

watch(
  () => result.value,
  result => {
    generateData(toRaw(result))
  },
  { immediate: true }
)

watch(
  () => visitsCount.value,
  result => {
    const count = Object.keys(toRaw(result)).length
    store.commit('setVisitsCount', count)
  },
  { immediate: true }
)

function generateData(result: any) {
  const feedsList = []
  for (var key in result) {
    if (Object.hasOwn(result, key)) {
      if (key === 'isDarkTheme') {
        store.commit('setPageTheme', result[key])
      } else {
        var val = result[key]
        feedsList.push({ ...val, showAnimation: false })
      }
    }
  }
  manageResultsData(feedsList)
  store.commit('setFeedsData', feedsList)
}

function manageResultsData(data: any) {
  const newArray: any = {
    1: { 1: [], 2: [], 3: [] },
    2: { 1: [], 2: [], 3: [] },
    3: { 1: [], 2: [], 3: [] },
    // 4: { 1: [], 2: [], 3: [] },
    5: { 1: [], 2: [], 3: [] },
  }
  data.forEach((item: any) => {
    newArray[item.compId][item.result].push(item)
  })
  store.commit('setResultData', newArray)
  isLoading.value = false
}

function fetchUserData() {
  isLoading.value = true
  const starCountRef = firebaseRef(db, `tudar-parba-result`)
  onValue(starCountRef, snapshot => {
    result.value = snapshot.val()
  })
}

function fetchVisitsCount() {
  writeVisitsCount()

  const starCountRef = firebaseRef(db, `tudar-parba-count`)
  onValue(starCountRef, snapshot => {
    visitsCount.value = snapshot.val()
  })
}
</script>

<style lang="scss">
@import '@/utils/color.scss';

* {
  padding: 0;
  margin: 0;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
</style>
