<template>
  <div class="footer" :class="{ 'footer-dark': isDarkTheme }">
    <div class="footer-items">
      <div class="social-links">
        <template v-for="(item, index) in socialLinks" :key="index">
          <fai
            :icon="['fa-brands', item.icon]"
            @click="item.action"
            class="social-icon"
          />
          <fai
            :icon="['fa-solid', item.icon]"
            @click="item.action"
            class="social-icon"
          />
        </template>
      </div>
      <div class="footer-text">
        <span>
          <i>
            Made with <fai icon="fa-solid fa-heart" /> and
            <fai icon="fa-solid fa-hammer" /> by
            <span @click="openAdminPage">prem</span>
          </i>
        </span>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import router from '@/router'
import store from '@/store'
import { ref, watch } from 'vue'

const isDarkTheme = ref(false)
const socialLinks = [
  {
    icon: 'fa-instagram',
    action: () => openSocialLink('https://www.instagram.com/_prem_kulal'),
  },
  {
    icon: 'fa-youtube',
    action: () => openSocialLink('https://www.youtube.com/@premnathkulal'),
  },

  {
    icon: 'fa-linkedin',
    action: () =>
      openSocialLink('https://in.linkedin.com/in/premnath-kulal-06329a169'),
  },
  {
    icon: 'fa-earth-asia',
    action: () => openSocialLink('https://premnathkulal.github.io/'),
  },
  {
    icon: 'fa-facebook-f',
    action: () => openSocialLink('https://www.facebook.com/premnath.kulal.5/'),
  },
]

watch(
  () => store.state.pageTheme,
  value => {
    isDarkTheme.value = value
  },
  { immediate: true }
)

function openSocialLink(mediaType: string) {
  window.open(mediaType, '_blank')
}

function openAdminPage() {
  router.push({ name: 'Admin' })
}
</script>

