<script setup lang="ts">
import { CompetitionList } from '@/constants/ui-data'
import { ref, watch } from 'vue'
import store from '@/store'

const showOptions = ref(false)
const competitionList = CompetitionList
const selectedOption = ref(CompetitionList[4])
const showSearchOption = ref(false)
const searchKey = ref('')
const isDarkTheme = ref(false)
let timeout = 1000

window.addEventListener('click', function (e: any) {
  if (
    !(
      document?.getElementById('options-list')?.contains(e.target) ||
      document.getElementById('options-icon')?.contains(e.target)
    )
  ) {
    showOptions.value = false
  }
})

function toggleShowOptions() {
  searchKey.value = ''
  showSearchOption.value = false
  showOptions.value = !showOptions.value
}

function toggleSearchOptions() {
  searchKey.value = ''
  showOptions.value = false
  showSearchOption.value = !showSearchOption.value
}

function selectOptionAction(id: string) {
  toggleShowOptions()
  const item = CompetitionList.find(item => id === item.id)
  if (item) {
    selectedOption.value = item
  }
  store.commit('setFilterId', id)
}

function filterByName() {
  clearTimeout(timeout)
  timeout = setTimeout(() => {
    store.commit('setSearchKey', searchKey.value)
  }, 500)
}

function togglePageTheme() {
  isDarkTheme.value = !isDarkTheme.value
  store.commit('setPageTheme', isDarkTheme.value)
}

watch(
  () => store.state.pageTheme,
  value => {
    isDarkTheme.value = value
  },
  { immediate: true }
)
</script>

