<template>
  <div class="admin">
    <div class="d-flex flex-column w-100 p-3" v-if="authenticated">
      <div class="input-group mb-4">
        <input
          v-model="name"
          type="text"
          class="form-control form-control-lg"
          placeholder="Name"
          aria-label="name"
          aria-describedby="name"
        />
      </div>
      <div class="input-group mb-4">
        <input
          v-model="mobileNumber"
          type="number"
          class="form-control form-control-lg"
          placeholder="Mobile Number"
          aria-label="mobile-number"
          aria-describedby="mobile-number"
        />
      </div>
      <div class="input-group mb-4">
        <input
          v-model="shortAddress"
          type="text"
          class="form-control form-control-lg"
          placeholder="Location"
          aria-label="location"
          aria-describedby="location"
        />
      </div>
      <div class="input-group mb-4">
        <input
          v-model="result"
          type="text"
          class="form-control form-control-lg"
          placeholder="Result"
          aria-label="result"
          aria-describedby="result"
        />
      </div>
      <div class="input-group mb-4">
        <input
          v-model="imageLink"
          type="text"
          class="form-control form-control-lg"
          placeholder="Image Link"
          aria-label="image-url"
          aria-describedby="image-url"
        />
      </div>
      <select
        v-model="mediaType"
        class="form-select mb-4"
        aria-label="Default select example"
      >
        <option value="video">Video</option>
        <option value="image">Image</option>
      </select>
      <select
        v-model="compId"
        class="form-select mb-4"
        aria-label="Default select example"
      >
        <option v-for="item in competitionList" :value="item.id" :key="item.id">
          {{ item.text }}
        </option>
      </select>
      <div class="input-group mb-4">
        <input
          v-model="date"
          type="date"
          class="form-control form-control-lg"
          aria-label="date"
          aria-describedby="date"
        />
      </div>
      <button
        type="button"
        class="btn btn-outline-primary mb-4"
        @click="addToFeedAction"
      >
        Add To Feed
      </button>
      <button
        type="button"
        class="btn btn-outline-danger cancel-btn"
        @click="cancelAction"
      >
        Cancel
      </button>
      <a class="mt-3" href="https://www.labnol.org/embed/google/drive/">
        Helper Web link
      </a>
    </div>
    <div class="d-flex flex-column w-100 p-3" v-else>
      <div class="input-group mb-4">
        <input
          v-model="username"
          type="text"
          class="form-control form-control-lg"
          placeholder="Username"
          aria-label="Username"
          aria-describedby="UserName"
        />
      </div>
      <div class="input-group mb-4">
        <input
          v-model="password"
          type="text"
          class="form-control form-control-lg"
          placeholder="Password"
          aria-label="password"
          aria-describedby="password"
        />
      </div>
      <button
        type="button"
        class="btn btn-outline-primary mb-4"
        @click="adminAuthHandler"
      >
        Submit {{ visitsCount }}
      </button>
      <button
        type="button"
        class="btn btn-outline-danger cancel-btn"
        @click="cancelAction"
      >
        Cancel
      </button>
    </div>
  </div>
</template>

<script setup lang="ts">
import router from '@/router'
import { ref, watch } from 'vue'
import { writeUserData } from '@/utils/firebase-config'
import { CompetitionList } from '@/constants/ui-data'
import store from '@/store'

const name = ref('')
const compId = ref('1')
const imageLink = ref('')
const mediaType = ref('image')
const mobileNumber = ref('')
const shortAddress = ref('')
const date = ref('')
const username = ref('')
const password = ref('')
const authenticated = ref(false)
const competitionList = CompetitionList.filter(item => item.id != '0')
const result = ref(0)
const visitsCount = ref(0)

watch(
  () => store.state.visitsCount,
  count => {
    visitsCount.value = count
  },
  { immediate: true }
)

function addToFeedAction() {
  const id = `${new Date().getTime()}-${mobileNumber.value}-${compId.value}`
  const data = {
    id,
    name: name.value,
    mobileNumber: mobileNumber.value,
    compId: compId.value,
    imageLink: imageLink.value,
    mediaType: mediaType.value,
    address: shortAddress.value,
    result: result.value,
    date: formateDate(date.value),
  }
  writeUserData(data)

  // name.value = ''
  // compId.value = ''
  // imageLink.value = ''
  // mobileNumber.value = ''
  // shortAddress.value = ''
  // date.value = ''
}

function cancelAction() {
  router.back()
}

function formateDate(date: string): string {
  const d = new Date(date)

  const s =
    d.getDate() + ' ' + d.toString().substr(4, 3) + ', ' + d.getFullYear()
  return s
}

function adminAuthHandler() {
  if (username.value === 'prem' && password.value === 'prem123') {
    authenticated.value = true
  }
}
</script>

