    <template v-for="(item, index) in data" :key="index">
      <div
        class="header-section"
        :class="{ 'header-section-dark': isDarkTheme }"
      >
        <div class="content-header">
          <div class="filter-section-text">
            <span class="text">
              {{ getCompetitionName(`${index}`) }}
            </span>
          </div>
        </div>
      </div>
      <template v-for="(result, key) in item" :key="`${index}-${key}`">
        <div
          v-for="(userInfo, userId) in result?.slice().reverse()"
          :key="`${index}-${key}-${userId}`"
          class="feeds-content"
          :class="{ 'feeds-content-dark': isDarkTheme }"
        >
          <div class="image-info">
            <div class="image-name">
              <span v-if="index.toString() !== '5'" class="result">
                {{ getResultName(`${userInfo.result}`) }}:
              </span>
              <span class="name">{{ userInfo.name }} </span>
            </div>
            <div class="image-description">
              <span class="address">
                <fai icon="fa-solid fa-location-dot" /> {{ userInfo.address }}
              </span>
            </div>
          </div>
          <div class="image-container" @dblclick="likeClickAction(userInfo.id)">
            <celebration-animation v-if="userInfo.showAnimation" />
            <img class="media" :src="`${userInfo.imageLink}`" alt="" />
          </div>
          <div class="image-info-bellow">
            <div class="image-description">
              <span class="com-type">
                {{ getCompetitionName(userInfo.compId) }}
              </span>
              <span class="com-type date">
                {{ userInfo.date }}
              </span>
            </div>
            <div class="image-info likes">
              <div
                class="likes-container"
                @click="likeClickAction(userInfo.id)"
              >
                <fai v-if="userInfo.isLikeClicked" icon="fa-solid fa-heart" />
                <fai v-else icon="fa-regular fa-heart" />
              </div>
            </div>
          </div>
        </div>
      </template>
    </template>
  </div>
