<script setup lang="ts">
import { ref, watch, defineProps } from 'vue'
import { CompetitionList, Result } from '@/constants/ui-data'
import store from '@/store'
import CelebrationAnimation from '@/components/CelebrationAnimation.vue'

const props = defineProps({
  feedsData: {
    type: Object,
  },
})

// const imageLinkPrefix = 'https://drive.google.com/uc?export=view&id='
const feedsItems = ref(props.feedsData)
const isDarkTheme = ref(false)
const data = ref()

watch(
  () => props.feedsData,
  data => {
    feedsItems.value = data
  },
  { immediate: true }
)

watch(
  () => store.state.pageTheme,
  value => {
    isDarkTheme.value = value
  },
  { immediate: true }
)

watch(
  () => store.state.resultData,
  () => {
    data.value = store.state.resultData
  },
  { immediate: true }
)

function likeClickAction(id: string) {
  feedsItems.value?.forEach((element: any) => {
    if (element.id === id) {
      element.isLikeClicked = !element.isLikeClicked
      if (element.isLikeClicked) {
        element.showAnimation = true
        setTimeout(() => {
          element.showAnimation = false
        }, 2500)
      }
    }
  })
}

function getCompetitionName(id: string) {
  const item = CompetitionList.find(item => id === item.id)
  return item?.text
}

function getResultName(id: string) {
  const item = Result.find(item => id === item.id)
  return item?.text
}
</script>

