<template>
  <div class="feeds-area" :class="{ 'feeds-area-dark': isDarkTheme }">
    <lottie-player
      v-if="isLoading || !data.length"
      class="lottie-player"
      autoplay
      loop
      mode="normal"
      src="/lottie-files/happy-diwali-boy.json"
      style="width: 200px"
    >
    </lottie-player>
    <template v-if="isLoading">
      <span class="loader"></span>
    </template>
    <div v-else-if="!isLoading && !data.length" class="no-data-text">
      Sorry No Items Found
    </div>
    <feeds-content v-else :feeds-data="data" />
    <div class="thanks-text">
      ವಿಜೇತರಿಗೆ ಹಾಗೂ ಸ್ಪರ್ಧೆಗೆ ಫೋಟೋ ಕಳುಹಿಸಿದ ಎಲ್ಲರಿಗೂ ಧನ್ಯವಾದಗಳು 🙇
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, watch } from 'vue'
import FeedsContent from '@/components/FeedsContent.vue'

import store from '@/store'

const isLoading = ref(false)
const data = ref([])
const isDarkTheme = ref(false)

watch(
  () => store.state.filterID,
  filterId => {
    if (filterId !== '0') {
      const test = store.state.feedsData.filter(
        (item: any) => item.compId === filterId
      )
      data.value = test
    } else {
      data.value = store.state.feedsData
    }
  },
  { immediate: true }
)

watch(
  () => store.state.setSearchKey,
  searchKey => {
    if (searchKey !== '') {
      const test = store.state.feedsData.filter((item: any) =>
        item.name.toLowerCase().includes(searchKey.toLowerCase())
      )
      data.value = test
    } else {
      data.value = store.state.feedsData
    }
  },
  { immediate: true }
)

watch(
  () => store.state.feedsData,
  () => {
    data.value = store.state.feedsData
  },
  { immediate: true }
)

watch(
  () => store.state.pageTheme,
  value => {
    isDarkTheme.value = value
  },
  { immediate: true }
)
</script>

